import React, { useState } from "react";

import { useHistory, Link } from "react-router-dom";

import { loginUserAccount } from "../api";

import "bootstrap/dist/css/bootstrap.min.css";

import { Button, Form, FormGroup, Label, Input } from "reactstrap";

const LoginForm = ({setUser}) => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const history = useHistory();

  return (
    <div
      className="RegisterForm">
      <Form
        style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
            width: "250px",
            marginTop: "150px",
            margin: "auto",
        }}
        onSubmit={async (event) => {
          event.preventDefault();

          try {
            const data = await loginUserAccount(username, password);
            if (data) {
                setUser(data)
                console.log(data)
                history.push("/alcohol");
            } else {
                alert('Username or Password Do Not Match!')
            }
            
          } catch (error) {}
        }}
      >
        <FormGroup>
          <Label for="username">Username</Label>
          <Input
            type="text"
            id="username"
            placeholder="Enter Username"
            required={true}
            value={username}
            onChange={(event) => setUsername(event.target.value)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="password">Password</Label>
          <Input
            type="password"
            id="password"
            placeholder="Enter Password"
            required={true}
            value={password}
            onChange={(event) => setPassword(event.target.value)}
          />
        </FormGroup>
        <Button style={{marginTop: '5px'}} type="submit">Login</Button>
        <h6>Need an account? <Link to='registerUserAccount'>Register Here</Link></h6>
      </Form>
    </div>
  );
};

export default LoginForm;
